@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html{box-sizing:border-box}*,*:before,*:after{box-sizing:inherit}body,h1,h2,h3,h4,h5,h6,p,ol,ul{font-weight:normal;margin:0;padding:0}body{font-family:"Roboto",sans-serif}ol,ul{list-style:none}img{height:auto;max-width:100%}
.container{max-width:960px;margin:0 auto}.loading{display:flex}.loading__text{justify-content:center;align-items:center}.movie{display:flex;flex-wrap:wrap;justify-content:space-between;margin-top:5rem}.movie__item{display:flex;flex-basis:48%;margin-top:3rem;background:#fff;box-shadow:4px 4px 14px 0px #cacaca;border-radius:10px;overflow:hidden}.movie__poster{width:40%}.movie__poster img{height:100%}.movie__content{width:60%;padding:1.5rem}.movie__release{margin-top:1rem;opacity:.6}.movie__overview{margin-top:1rem}@media(max-width: 960px){body{font-size:14px}.container{padding:3rem}.movie__item{flex-basis:100%}}@media(max-width: 480px){.movie__item{display:block}.movie__poster{width:auto}.movie__content{width:auto}}
